.menu-picture {
  position: absolute;
  bottom: 66px;
}

.menu-picture__img--wrapper {
  width: 132px;
  height: 132px;
  background: linear-gradient(0deg, #9cbe8fb3, #d9d9d9), #d9d9d9;
  border-radius: 50%;
  position: absolute;
  bottom: 59px;
  box-shadow: 0 5.26027px 19.726px #0a881740;
}

.checkbox:hover .menu-picture__img--wrapper, .chekbox:focus .menu-picture__img--wrapper {
  box-shadow: 0px 5.26027px 19.726pxrgba (0, 0, 0, .25);
  background: linear-gradient(0deg, #d5753fb3, #d9d9d9), #d9d9d9;
}

.menu-border {
  width: 132px;
  height: 135px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.checkbox {
  display: flex;
  position: relative;
}

.checkbox-menu {
  width: 132px;
  height: 135px;
  border: 2px solid #96af8c;
  border-radius: 15px;
}

.checkbox-menu:hover, .menu-border:focus {
  border: 2px solid #d6936d;
}

.checkbox input:checked ~ .checkbox__checkmark {
  width: 132px;
  height: 135px;
  background-color: #efd478;
  border: 2px solid #efd478;
  border-radius: 15px;
  position: absolute;
}

.checkbox input:checked ~ .menu-picture__img--wrapper {
  background: linear-gradient(0deg, #f8d24eb3, #d9d9d9), #d9d9d9;
  box-shadow: 0 5.26027px 19.726px #99721740;
}

input[type="checkbox"] {
